.picapica-root {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #ffffff;
  color: #111827; /* near-black */
  font-family: 'Montserrat', system-ui, -apple-system, 'Segoe UI', Roboto, 'Helvetica Neue', Arial;
}

.picapica-container {
  max-width: 780px;
  padding: 48px 24px;
  text-align: center;
}

.picapica-header h1 {
  font-size: 40px;
  margin: 0 0 12px 0;
  font-weight: 800;
}

.picapica-header .subtitle {
  font-size: 16px;
  color: #4b5563; /* gray-600 */
  margin-bottom: 28px;
}

.photostrip {
  max-width: 320px;
  width: 60%;
  height: auto;
  display: block;
  margin: 16px auto 20px auto;
  box-shadow: 0 8px 20px rgba(0,0,0,0.08);
}

.start-button {
  display: inline-block;
  margin-top: 8px;
  padding: 14px 36px;
  background: #111827; /* dark */
  color: #fff;
  text-decoration: none;
  border-radius: 6px;
  font-weight: 700;
}

.start-button:hover {
  background: #0b1220;
}

.tagline {
  margin-top: 18px;
  color: #6b7280;
  font-size: 14px;
}

.picapica-footer {
  margin-top: 36px;
  color: #6b7280;
  font-size: 13px;
}

.picapica-footer a {
  color: #111827;
  text-decoration: underline;
}
